import test from 'ava';

import {
  createMongoose
} from '../test_helper';

test.before('start server', async t => {
  await MongoDBServer.start();
})

test.beforeEach('create mongoose connection', async (t) => {
  const db = await createMongoose();
  t.context.db = db;
});

test('query user via `find` and `findOne`', async (t) => {
  const { db } = t.context;
  const User = db.models['user'];

  await db.collections.users.drop();

  const NEW_USER_NAME = 'Joe';
  const joe = new User({ name: NEW_USER_NAME });
  await joe.save();
  const users = await User.find({ name: NEW_USER_NAME });

  t.is(
    users[0]._id.toString(),
    joe._id.toString(),
    'the id of the created object is same as the queried one'
  );

  const user = await User.findOne({ _id: joe._id });
  t.is(user.name, NEW_USER_NAME, 'the name of the created object is same as the queried one');

});

